//
// Made with  by Maysara.



//  PACK 

    import type { JSXElement } from '@minejs/jsx';
    import { signal } from '@minejs/signals';

// 



//  TYPE 

    export interface AlertProps {
        // Status
        status?: 'info' | 'success' | 'warning' | 'error';

        // Variant
        variant?: 'solid' | 'subtle' | 'left-accent' | 'top-accent';

        // Title
        title?: string | JSXElement;

        // Description
        description?: string | JSXElement;
        children?: JSXElement | string;

        // Icon
        icon?: JSXElement;
        showIcon?: boolean;

        // Closable
        closable?: boolean;
        onClose?: () => void;

        // Actions
        actions?: JSXElement;

        // Styling
        class?: string;

        // Accessibility
        role?: 'alert' | 'status';
        'aria-live'?: 'polite' | 'assertive';
    }

// 



//  CORE 

    export function Alert(props: AlertProps): JSXElement | null {
        const {
            status = 'info',
            variant = 'subtle',
            title,
            description,
            children,
            icon,
            showIcon = true,
            closable = false,
            onClose,
            actions,
            class: className,
            role = 'alert',
            'aria-live': ariaLive = 'polite',
            ...restProps
        } = props;

        const isVisible = signal(true);

        const handleClose = () => {
            isVisible.set(false);
            onClose?.();
        };

        if (!isVisible()) return null;

        // Status colors using @mineui semantic tokens
        const statusColors = {
            solid: {
                info: ['bg-color-primary', 'text-white', 'border border-color-primary'],
                success: ['bg-color-success', 'text-white', 'border border-color-success'],
                warning: ['bg-color-warning', 'text-white', 'border border-color-warning'],
                error: ['bg-color-error', 'text-white', 'border border-color-error']
            },
            subtle: {
                info: ['bg-color-primary-subtle', 'text-primary', 'border border-color-primary'],
                success: ['bg-color-success-subtle', 'text-primary', 'border border-color-success'],
                warning: ['bg-color-warning-subtle', 'text-primary', 'border border-color-warning'],
                error: ['bg-color-error-subtle', 'text-primary', 'border border-color-error']
            },
            'left-accent': {
                info: ['bg-color-primary-subtle', 'text-primary', 'border-s-4', 'border-color-primary'],
                success: ['bg-color-success-subtle', 'text-primary', 'border-s-4', 'border-color-success'],
                warning: ['bg-color-warning-subtle', 'text-primary', 'border-s-4', 'border-color-warning'],
                error: ['bg-color-error-subtle', 'text-primary', 'border-s-4', 'border-color-error']
            },
            'top-accent': {
                info: ['bg-color-primary-subtle', 'text-primary', 'border-t-4', 'border-color-primary'],
                success: ['bg-color-success-subtle', 'text-primary', 'border-t-4', 'border-color-success'],
                warning: ['bg-color-warning-subtle', 'text-primary', 'border-t-4', 'border-color-warning'],
                error: ['bg-color-error-subtle', 'text-primary', 'border-t-4', 'border-color-error']
            }
        };

        // Default icons
        const defaultIcons = {
            info: (
                <svg class="size-5" fill="currentColor" viewBox="0 0 20 20">
                    <path fill-rule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clip-rule="evenodd" />
                </svg>
            ),
            success: (
                <svg class="size-5" fill="currentColor" viewBox="0 0 20 20">
                    <path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clip-rule="evenodd" />
                </svg>
            ),
            warning: (
                <svg class="size-5" fill="currentColor" viewBox="0 0 20 20">
                    <path fill-rule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clip-rule="evenodd" />
                </svg>
            ),
            error: (
                <svg class="size-5" fill="currentColor" viewBox="0 0 20 20">
                    <path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd" />
                </svg>
            )
        };

        const classes = [
            'flex gap-3 p-4 rounded-lg',
            ...statusColors[variant][status],
            className
        ]
            .filter(Boolean)
            .join(' ');

        const iconElement = showIcon && (icon || defaultIcons[status]);

        return (
            <div class={classes} role={role} aria-live={ariaLive} {...restProps}>
                {iconElement && (
                    <div class="shrink-0 pt-0.5">
                        {iconElement}
                    </div>
                )}

                <div class="flex-1 min-w-0">
                    {title && (
                        <div class="font-semibold mb-1">
                            {title}
                        </div>
                    )}

                    {(description || children) && (
                        <div class="text-sm opacity-90">
                            {description || children}
                        </div>
                    )}

                    {actions && (
                        <div class="mt-3">
                            {actions}
                        </div>
                    )}
                </div>

                {closable && (
                    <button
                        onClick={handleClose}
                        class="shrink-0 p-1 rounded hover:bg-black hover:bg-opacity-10 transition"
                        aria-label="Close alert"
                    >
                        <svg class="size-4" fill="currentColor" viewBox="0 0 20 20">
                            <path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd" />
                        </svg>
                    </button>
                )}
            </div>
        );
    }

// 
